import {
    StreamlitComponentBase,
    withStreamlitConnection,
} from "streamlit-component-lib";
import React, { ReactNode } from "react";

import App from "./App.js";

interface State {
  isFocused: boolean
};

class MyComponent extends StreamlitComponentBase<State> {
    public state = { isFocused: false };

    public render = (): ReactNode => {
    	const data = this.props.args["data"]; // Python bytes
	var byteArray = new Uint8Array(data);
	const blob = new Blob([byteArray], { type: 'audio/wav' });

    	const config = this.props.args["config"]; // dictionary
	
	const { theme } = this.props;
	const style: React.CSSProperties = {};
	
	if (theme) {
	    const borderStyling = `1px solid ${this.state.isFocused ? theme.primaryColor : "gray"}`;
	    style.border = borderStyling;
	    style.outline = borderStyling;
	}
	
	return (
	    <span>
		<App blob={blob} config={config}/>
	    </span>
	);
    }
}
						    
export default withStreamlitConnection(MyComponent);
