import React, {
  useCallback,
  useRef,
  useState,
  useMemo
} from "react";
import {Streamlit} from "streamlit-component-lib";
import styled from "styled-components";
import { WaveSurfer, WaveForm } from "wavesurfer-react";
import colormap from "colormap";
import "./App.css";
import SpectrogramPlugin from "wavesurfer.js/dist/plugins/spectrogram";

function App({blob, config}) {
    const [isLoaded, setIsLoaded] = useState(false);
    
    const colors = useMemo(() => colormap(config.colormap), [config]);

    const plugins = useMemo(() => {
	return [
	    {
		key: "spectrogram",
		plugin: SpectrogramPlugin,
		options: {
		    labels: config.spectrogram.labels,
		    height: config.spectrogram.height,
		    colorMap: colors
		}
	    },
	].filter(Boolean);
    }, [config, colors]);
    
    const wavesurferRef = useRef();
    
    const handleWSMount = useCallback(
	(waveSurfer) => {
	    wavesurferRef.current = waveSurfer;
	    
	    if (wavesurferRef.current) {
		wavesurferRef.current.loadBlob(blob);
		
		wavesurferRef.current.on("ready", () => {
		    console.log("WaveSurfer is ready");
		    Streamlit.setFrameHeight();
		    setIsLoaded(true);
		});
		
		wavesurferRef.current.on("loading", (data) => {
		    console.log("loading --> ", data);
		});
		
		if (window) {
		    window.surferidze = wavesurferRef.current;
		}
	    }
	},
	[blob]
    );

  return (
    <div className="App">
	<WaveSurfer
	    height={config.waveform.height}
	    mediaControls={config.mediaControls}
	    normalize={config.waveform.normalize}
	    plugins={plugins}
	    onMount={handleWSMount}
	    cursorColor={config.cursor.color}
	    cursorWidth={config.cursor.width}
	    waveColor={config.waveform.color}
	    progressColor={config.waveform.progressColor}
	    container="#waveform"
	>
        <WaveForm>
        </WaveForm>
      </WaveSurfer>
    </div>
  );
}

export default App;
